
import React from 'react';
import LinearProgress from 'material-ui/LinearProgress';

class User extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			data: {}
		};
	}

	fetchData() {
		fetch('https://api.lootbox.eu/pc/us/' + this.props.user + '/quick-play/allHeroes/', {
			method: 'get'
		}).then(response => response.json())
		  .then(data => {
		  	this.setState({data: data});
	    })
	    .catch(err => { 
		    console.log('Fetch Error :-S', err);
		  });
	}

	componentDidMount() {
		this.fetchData();
	}

	isEmpty(object) {
	  for(var key in object) {
	    if(object.hasOwnProperty(key)) {
	      return false;
	    }
	  }
	  return true;
	}

	render() {
		var user = this.state.data;
		if (!this.isEmpty(user)) {
			return (
				<div>
				  	<div><b>{(user.battletag).split('-')[0]}</b></div>
				  	<div>Kills: {user.Eliminations}</div>
				  	<div>Deaths: {user.Deaths}</div>
				  	<div>Time played: {user.TimePlayed} hours</div>
				</div>
			);
		}	else {
			return (
				<div>
				  	<div><LinearProgress style={{height: '50px', width: '150'}} mode="indeterminate" /></div>
				</div>
			);
		}
	}
}
export default User;


